<template>
  <Carousel :settings="settings" :breakpoints="breakpoints">
    <Slide
      v-for="trainerInfo in trainers"
      :key="trainerInfo.id"
      class="trainer-cards"
    >
      <!-- <div class="carousel__item">{{ slide }}</div> -->

      <div class="carousel__item card">
        <img
          class="card-img"
          v-bind:src="trainerInfo.img"
          alt="tainer picture"
        />
        <p class="name">{{ trainerInfo.name }}</p>
        <router-link
          :to="`/trainer-page/${trainerInfo.id}`"
          class="more bg-black-btn"
          >Read more</router-link
        >
      </div>
    </Slide>

    <template #addons>
      <Navigation />
    </template>
  </Carousel>
</template>

<script>
import { defineComponent } from "vue";
import { Carousel, Navigation, Slide } from "vue3-carousel";

import "vue3-carousel/dist/carousel.css";
export default defineComponent({
  name: "Breakpoints",
  components: {
    Carousel,
    Slide,
    Navigation,
  },
  data: () => ({
    // carousel settings
    settings: {
      itemsToShow: 1,
      snapAlign: "center",
    },
    // breakpoints are mobile first
    // any settings not specified will fallback to the carousel settings
    breakpoints: {
      // 700px and up
      700: {
        itemsToShow: 3,
        snapAlign: "center",
      },
      // 1024 and up
      1024: {
        itemsToShow: 4,
        snapAlign: "start",
      },
    },
  }),
  computed: {
    trainers() {
      return this.$store.getters["getPros"];
    },
  },
});
</script>
